/* CategoryForm.module.css */

/* Genel container */
.container {
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    max-width: 600px;
    margin: 0 auto;
    margin-top: 20px;
  }
  
  /* Başlıklar */
  .title {
    font-size: 1.5rem;
    font-weight: bold;
    color: #333;
    margin-bottom: 1rem;
  }
  
  .subTitle {
    font-size: 1.25rem;
    font-weight: 600;
    color: #555;
    margin-top: 1.5rem;
    margin-bottom: 0.5rem;
  }
  
  /* Form alanı */
  .form {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  /* Input alanı */
  .input {
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 6px;
    font-size: 1rem;
    outline: none;
    transition: border-color 0.3s ease;
  }
  
  .input:focus {
    border-color: #007bff;
  }
  
  /* Submit buton */
  .button {
    padding: 12px 20px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 6px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .button:hover {
    background-color: #0056b3;
  }
  
  /* Kategori listesi */
  .list {
    list-style: none;
    padding-left: 0;
    margin-top: 1rem;
  }
  
  .listItem {
    font-size: 1rem;
    color: #333;
    background-color: #f1f1f1;
    padding: 8px;
    border-radius: 6px;
    margin-bottom: 8px;
  }
  
  .listItem:last-child {
    margin-bottom: 0;
  }
  
  /* Boş kategori mesajı */
  .emptyMessage {
    color: #999;
    font-style: italic;
  }
  