/* page.module.css */
.container {
  background-color: #f7f7f7; /* Soft Gri Renk */
  min-height: 100vh;
  padding: 20px;
}

.dark-mode .container {
  background-color: #1f2a44;
  color: white;
}

.page-title {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 1rem;
}

.button {
  padding: 10px 20px;
  background-color: #007BFF;
  color: white;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.button:hover {
  background-color: #a100b3;
}

.button:focus {
  outline: none;
}

.button + .button {
  margin-left: 10px;
}
