.list-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    background-color: #ffffff;
  }
  
  .list-container h1 {
    text-align: center;
    margin-bottom: 20px;
    font-size: 24px;
    color: #333;
  }
  
  .table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
  }
  
  .table th,
  .table td {
    border: 1px solid #ddd;
    padding: 12px 15px;
    text-align: left;
  }
  
  .table th {
    background-color: #007bff;
    color: #ffffff;
    font-weight: bold;
  }
  
  .table tr:nth-child(even) {
    background-color: #f9f9f9;
  }
  
  .table tr:hover {
    background-color: #f1f1f1;
  }
  
  .empty-message {
    text-align: center;
    font-size: 16px;
    color: #666;
  }
  .budget-limit {
    margin-bottom: 20px;
    font-size: 1.2rem;
    color: #2c3e50;
  }
  .dark-mode .container {
    background-color: #1f2a44; /* Koyu Gece Mavisi */
    color: white; /* Beyaz metin rengi */
  }